"use client"

import { useEffect, useState } from "react"
import { useToast } from "@/components/ui/use-toast"

type Movement = {
  id: number
  movement_type: string
  quantity: number
  movement_date: string
  stock_before: number
  stock_after: number
  notes?: string
}

export function StockMovementsNotifier({ apiUrl }: { apiUrl: string }) {
  const { toast } = useToast()
  const [lastMovementId, setLastMovementId] = useState<number | null>(null)

  useEffect(() => {
    if (!apiUrl) return

    const interval = setInterval(async () => {
      try {
        const response = await fetch(`${apiUrl}/movements?limit=1`)
        const data: Movement[] = await response.json()

        if (!data || data.length === 0) return

        const latest = data[0]

        if (latest.id !== lastMovementId) {
          setLastMovementId(latest.id)

          toast({
            title: `Movimentação: ${latest.movement_type}`,
            description: `
              Quantidade: ${latest.quantity}
              Estoque: ${latest.stock_before} → ${latest.stock_after}
            `,
          })
        }
      } catch (error) {
        console.error("Erro ao buscar movimentações", error)
      }
    }, 5000)

    return () => clearInterval(interval)
  }, [apiUrl, lastMovementId, toast])

  return null
}
